const {ccclass, property} = cc._decorator;

@ccclass
export default class ApiManager extends cc.Component {

    @property(cc.Label)
    label: cc.Label = null;

    @property
    text: string = 'hello';

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}
    static instance: ApiManager = null;

    GetRequest(url){

        var xhr = cc.loader.getXMLHttpRequest();
        var msg;
        //var url = "https://www.staging.adminv2.talentlitmus.com/players/leaderboard?email=test11@talentlitmus.com&divisionId=5eb4f283edc4cb9091964886";
        cc.log(url);
        xhr.open("GET", url , true);
        //xhr.setRequestHeader('api-key', "MYLcvDAg5175KH5vTvGtvanxLtFNr8yU");
        
        xhr.onerror = function (){
          console.log("Connection Error");
        }
         // xhr.onload
        xhr.onreadystatechange = function () {
    
            if (xhr.readyState === 4 && (xhr.status >= 200 && xhr.status < 300)) {
                //cc.log(xhr.responseText);
                cc.game.emit('WeatherApiResponce' , xhr.responseText);
            }
            if(xhr.readyState === 4 && xhr.status >= 400){
                console.log("Server Error");
            }
        };
        xhr.send();
    }
    // update (dt) {}
}
